import React, { Component } from 'react';

import Main from './components/main';
import Auth from './components/auth';
import Reg from './components/reg';
import Profile from './components/profile';
import Top100 from './components/top100';
import MyList from './components/myList';
import List from './components/list';

import NotificationAlert from 'react-notification-alert';

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Table,
  Container } from 'reactstrap';

class App extends Component {

  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.selectSection = this.selectSection.bind(this);
    this.state = { isOpen: false };
  }

  toggle() { this.setState({ isOpen: !this.state.isOpen }); }  
  selectSection (section) { localStorage.section = section; }

	 myFunc() {
	 	this.refs.notify.notificationAlert(
	 		{
			    place: 'br',
			    message: (
			        <div>
			            <div>
			                Welcome to Now UI Dashboard React
			            </div>
			        </div>
			    ),
			    type: "info",
			    icon: "",
			    autoDismiss: 5
			}
	 	);
	 }

  render() {
    return (
      <div>

      	<NotificationAlert ref="notify" />

         <Navbar color="light" light expand="md">
          <NavbarBrand href="/" onClick={() => this.selectSection('main')}> </NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink href="/" active={localStorage.section === 'auth'} onClick={() => this.selectSection('auth')}></NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/" active={localStorage.section === 'reg'}  onClick={() => this.selectSection('reg')}></NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/" active={localStorage.section === 'profile'} onClick={() => this.selectSection('profile')}> </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/" active={localStorage.section === 'top100'} onClick={() => this.selectSection('top100')}> 100</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/" active={localStorage.section === 'myList'} onClick={() => this.selectSection('myList')}> </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/" active={localStorage.section === 'list'} onClick={() => this.selectSection('list')}> </NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>

        <br />

		<Container>
		
        	<button onClick={() => this.myFunc()}>Hey</button>
        
        	{ localStorage.section === 'main' || !localStorage.section ? <Main /> : '' }
        	{ localStorage.section === 'auth' ? <Auth /> : '' }	
			{ localStorage.section === 'reg' ? <Reg /> : '' }	
			{ localStorage.section === 'profile' ? <Profile /> : '' }	
			{ localStorage.section === 'top100' ? <Top100 /> : '' }
			{ localStorage.section === 'myList' ? <MyList /> : '' }
			{ localStorage.section === 'list' ? <List /> : '' }

      	</Container>

      </div>
    );
  }
}

export default App;



